:root {
  --color-background: #dedfe5;
  --color-base-layer: #bbc2d4;
  --color-placeholder: var(--color-base-layer);
  --color-primary: #2c2021;
  --color-secondary: #ffffff;
  --color-redtail: #aa4444;
  --color-orange: #dd7d41;
  --color-crystalBell: #efefef;

  --lightShadow: #6f6f6f;
  --shadow: #3f3f3f;
  --darkShadow: #0f0f0f;

  --border: 1.5px solid;

  --transition: .3s ease;
}

::selection {
  color: var(--color-crystalBell);
  background-color: var(--lightShadow);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  @include flex_column;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  height: 100%;
  font-size: calc(0.8em + 1vw);
  line-height: 1.5;
  font-family: $montserrat;
  background-color: var(--color-background);
  color: var(--color-primary);
  position: relative;
}

main {
  flex: 1 0 auto;
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
  transition: var(--transition);

  &:focus {
    outline: 0;
  }

  &:-moz-focusring {
    color: var(--color-orange);
    outline: var(--color-orange) auto 0.5px;
  }

  &:focus-visible {
    color: var(--color-orange);
    outline: var(--color-orange) auto 0.5px;
  }

  &:active {
    opacity: 0.8;
  }
}

input,
button,
textarea,
select {
  font: inherit;
  outline: none;
}

input[type="search"],
input[type="email"] {
  appearance: none;
  -webkit-appearance: none;
  border-radius: 0;
  -webkit-border-radius: 0;
  outline: 0;
}

svg {
  fill: var(--color-primary);

  &:hover,
  &:focus {
    fill: var(--color-orange);
  }

  &:active {
    opacity: 0.8;
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.overlay {
  --transition: 0.5s ease-in-out;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity var(--transition)
}

.overflow {
  overflow: hidden;
}

.page-wrapper {
  @include flex_column;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  position: relative;
}

.page-footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;

  @include tablet {
    padding: 0 20px;
  }
}

.btn {
  @include btn-reset;
  @include flex_center;
  position: relative;
  padding: 0 10px;
  min-height: 50px;
  text-align: center;
  font-size: 1rem;
  transition: var(--transition);
  outline: 2px solid transparent;
  border: var(--border) var(--color-secondary);
  color: var(--color-secondary);
  max-width: 275px;
  width: 100%;

  &--orange {
    border: var(--border) var(--color-orange);
    background-color: var(--color-orange);
  }

  &:hover,
  &:focus {
    background: var(--color-orange);
    border-color: var(--color-orange);
    outline: var(--border) var(--color-orange);
    outline-offset: 4px;
  }

  @include mobile {
    font-size: 1rem;
  }
}

.link {
  display: inline-block;
  padding: 0.2em 0.5em;
  border-bottom: 1px solid transparent;

  &:hover,
  &:focus {
    color: var(--color-orange);
    border-bottom: 1px solid var(--color-orange);
  }

  @include tablet {
    &:hover,
    &:focus {
      color: var(--color-primary);
      border-bottom: 1px solid transparent;
    }
  }
}
